body, html {
 padding: 0;
 margin: 0;
 font-family: 'Montserrat', sans-serif;
}

:root {
 --clr-primary-1: #BF40BF;
 --clr-primary-2: #5D3FD3;
}


/*=========================================================
                    LOGIN_PAGE STYLES
==========================================================*/
#login-container,
#signup-container {
 background-image: linear-gradient(to bottom right, #BF40BF, #5D3FD3);
 background-size: cover;
 background-repeat: no-repeat;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;
 height: 100vh;
}

#login-container {
  height: 100vh;
}

.login-container {
 border: 2px solid #fff;
 border-radius: 10px;
 padding: 20px;
 width: 70vw;
 max-width: 350px;
 margin: 2rem 0;
 background-color: white;
}

.login-title {
 font-size: 2rem;
 font-weight: bold;
 text-align: center;
 text-transform: capitalize;
 letter-spacing: 1px;
}

.login-container input {
 border: none;
 border-bottom: 1px solid black;
 background-color: transparent;
 outline: none;
 font-size: 1rem;
 padding: 0.5rem 0;
 width: 100%;
 margin-bottom: 1rem;
 color: var(--clr-primary-2);
}

.login-container #password,
.login-container #email {
 width: 95.5%;
 margin-top: 1rem;
}

.login-container #login {
 background-image: linear-gradient(to right, #BF40BF, #5D3FD3);
 border-bottom: none;
 margin-top: 2rem;
 padding: 1rem;
 font-weight: 500;
 font-size: 1.2rem;
 color: #fff;
 border-radius: 5px;
}

.signup-info,
.login-info {
 margin: 2rem;
 font-size: .8rem;
 display: flex;
 justify-content:  center;
}

.login-container span,
.signup-container span {
 color: #5D3FD3;
 font-weight: bold;
 margin-left: .5rem;
}

/*=========================================================
                      SIGNUP_PAGE STYLES
==========================================================*/
.signup-container {
 border: 2px solid #fff;
 border-radius: 10px;
 padding: 20px;
 width: 70vw;
 max-width: 350px;
 margin: 2rem 0;
 background-color: white;
}

.signup-container h1 {
 font-size: 1.5rem;
 font-weight: bold;
 text-align: center;
 text-transform: uppercase;
 letter-spacing: 1px;
}

.signup-container .names {
 display: flex;
 flex-direction: row;
 align-items: center;
 justify-content: center;
}

.signup-container #firstname,
.signup-container #lastname {
 border: none;
 border-bottom: 1px solid black;
 background-color: transparent;
 outline: none;
 font-size: .8rem;
 padding: .5rem 0;
 width: 47%;
 margin-bottom: 1rem;
 color: var(--clr-primary-2);
}

.signup-container #lastname {
 margin-left: 1rem;
}

.signup-container #email,
.signup-container #password,
.signup-container #retype-password {
 width: 98%;
 margin-top: 1rem;
 margin-bottom: 1rem;
 border: none;
 border-bottom: 1px solid black;
 background-color: transparent;
 outline: none;
 font-size: .8rem;
 padding: 0.5rem 0;
 margin-bottom: 1rem;
 color: var(--clr-primary-2);
}

.signup-container #signup {
 background-image: linear-gradient(to right, #BF40BF, #5D3FD3);
 border-bottom: none;
 margin-top: 1rem;
 padding: 1rem;
 font-weight: 500;
 font-size: 1.2rem;
 color: #fff;
 align-self: center;
 width: 100%;
 border-radius: 5px;
 border: none;
 outline: none;
 text-transform: uppercase;
 letter-spacing: 1px;
 margin: 1rem 0;
}

/*Alert message styles*/
.alert {
  font-size: .8rem;
  padding: .5rem;
  border-radius: 5px;
  letter-spacing: 1px;
  margin: 0;
  font-weight: 400;
  color: white;
}

.alert-success {
  background-color: hsl(120, 88%, 85%);
}

.alert-failed {
  background-color: hsl(0, 88%, 92%);
  color: hsl(0, 88%, 45%);
  border: 2px solid hsl(0, 88%, 45%);
}

/* Password wrapper styles */
.password-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}

#password-eye {
  position: absolute; 
  right: 1rem; 
}

/* Loader Styles */
.loader {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    background-color: hsl(240, 88%, 95%);
    z-index: 1;
    bottom: 0;
  }

/*=========================================================
                    HOME_PAGE STYLES
==========================================================*/
.home-page-container {
  background-image: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home-page-container h1 {
  text-align: center;
  color: var(--clr-primary-2);
}

.home-page-container button {
    width: 200px;
    margin: 2rem auto;
    background-color: var(--clr-primary-1);
    color: white;
    padding: 1rem;
    font-size: 1.3rem;
    border-radius: 10px;
    border: none;
    outline: none;
    
}

.signup-container #sign-up {
   border: 1rem 0;
}
   

@media screen and (min-width: 600px) {

 body,
 html {
  font-size: 18px;
 }
}

@media screen and (min-width: 1000px) {

 .login-container span,
 .signup-container span,
 #password-eye {
  cursor: pointer;
 }

 .login-container span:hover {
  text-decoration: underline;
 }

 .login-container #login,
 .signup-container #signup {
  padding: 1rem;
  font-size: 1.2rem;
  cursor: pointer;
 }

.login-container #login:hover,
.signup-container #signup:hover {
  background-color: hsl(240, 88%, 95%);
  color: black;
  border: 3px solid var(--clr-primary-2);
  background-image: none;
  transition: 200ms ease-in-out;
}

.home-page-container button:hover {
   background-color: hsl(240, 88%, 95%);
   color: black;
   border: 2px solid var(--clr-primary-2);
}

.home-page-container button {
  cursor: pointer;
}

}

@media screen and (min-width: 1400px) {

 body,
 html {
  font-size: 20px;
 }

 .login-container input,
 .signup-container input {
  font-size: .8rem;
 }

 .login-container #login,
 .signup-container #signup {
  font-size: 1.2rem;
  padding: .8rem;
 }

 .signup-info {
  font-size: .7rem;
 }
}